.Header_Navbar {
  width: 100%;
  height: 60px;
  background: rgb(255, 255, 255);
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-around;
  align-items: center;
  .Header_Navbar-Wrapper {
    width: 1200px;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    list-style: none;
  }
}
